.ui.modals.active {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    .ui.modal {
        background-color: #fff;
        padding: 1em;
        border-radius: 0;
        min-width: 300px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    }
}

